<template>
  <div class="hello">
    <el-row :gutter="20" class="functionalArea">
      <el-col :span="6">
        <el-button-group>
          <el-button type="primary" icon="el-icon-plus" @click="addBtnFn"></el-button>
          <el-button type="primary" icon="el-icon-share"></el-button>
          <el-button type="primary" icon="el-icon-delete" @click="clearBtnFn"></el-button>
        </el-button-group>
      </el-col>
      <el-col :span="6">
        <el-form>
          <el-form-item label="主题" :label-width="formLabelWidth">
            <el-select v-model="selectThemeId" @change="changeThemeFn" placeholder="请选择主题" style="width:100%;">
              <el-option v-for="(item, index) in themeList" :key="index" :label="item.label" :value="item.val"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :span="6">
        <el-form>
          <el-form-item label="事项" :label-width="formLabelWidth">
            <el-select v-model="selectMatterId" @change="changeMatterFn" placeholder="请选择主题" style="width:100%;">
              <el-option v-for="(item, index) in matterList" :key="index" :label="item.label" :value="item.val"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :span="6">
        <el-input placeholder="请输入内容" v-model="searchTheme" clearable>
          <template slot="append">
            <el-button type="success" plain>搜索</el-button>
          </template>
        </el-input>
      </el-col>
    </el-row>

    <el-table
            ref="multipleTable"
            :data="tableData"
            style="width: 100%"
            :header-cell-style="headerCellStyle"
            :cell-style="cellStyle"
            @select="handleSelectionChange"
            @select-all="selectAllFn"
    >
      <el-table-column
              type="selection"
              width="55">
      </el-table-column>
      <el-table-column
              label="序号"
              width="60">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.index }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="材料清单"
              width="300">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.name }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="准备要求"
              width="260">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.require }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="审查要求"
              width="300">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.permit }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="操作"
              min-width="240"
      >
        <template slot-scope="scope">
          <el-button
                  size="mini"
                  @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="添加要素" :visible.sync="addFormVisible" width="800px">
      <el-form :model="form">
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="事项" :label-width="formLabelWidth">
              <el-input v-model="form.item" autocomplete="off" disabled></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="要素" :label-width="formLabelWidth">
              <el-input v-model="form.name" placeholder="请输入要素" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="流程工序" :label-width="formLabelWidth">
              <el-select v-model="form.process" placeholder="请选择行使层级" style="width:100%;">
                <el-option v-for="(item, index) in processList" :key="index" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="必要" :label-width="formLabelWidth">
              <el-select v-model="form.need" placeholder="请选择行使层级" style="width:100%;">
                <el-option v-for="(item, index) in needList" :key="index" :label="item.label" :value="item.val"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="形式要点" :label-width="formLabelWidth">
              <el-input
                      type="textarea"
                      autosize
                      placeholder="请输入内容"
                      v-model="form.require">
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="许可要点" :label-width="formLabelWidth">
              <el-input
                      type="textarea"
                      autosize
                      placeholder="请输入内容"
                      v-model="form.permit">
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addSureFn">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="编辑要素" :visible.sync="fixFormVisible" width="800px">
      <el-form :model="form1">
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="事项" :label-width="formLabelWidth">
              <el-input v-model="form1.item" autocomplete="off" disabled></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="要素" :label-width="formLabelWidth">
              <el-input v-model="form1.name" placeholder="请输入要素" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="流程工序" :label-width="formLabelWidth">
              <el-select v-model="form1.process" placeholder="请选择工序" style="width:100%;">
                <el-option v-for="(item, index) in processList" :key="index" :label="item.name" :value="item.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="必要" :label-width="formLabelWidth">
              <el-select v-model="form1.need" placeholder="请选择行使层级" style="width:100%;">
                <el-option v-for="(item, index) in needList" :key="index" :label="item.label" :value="item.val"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="形式要点" :label-width="formLabelWidth">
              <el-input
                      type="textarea"
                      autosize
                      placeholder="请输入内容"
                      v-model="form1.require">
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="许可要点" :label-width="formLabelWidth">
              <el-input
                      type="textarea"
                      autosize
                      placeholder="请输入内容"
                      v-model="form1.permit">
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="fixFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="fixSureFn">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
// import { baseUrl } from '@/api/unsplash/baseUrl'
export default {
  name: 'ATheme',
  data () {
    return {
      selectThemeId: '',
      selectThemeName: '',
      themeList: [],

      selectMatterId: '',
      selectMatterName: '',
      matterList: [],

      searchTheme: '',
      tableData: [],
      // baseUrl: baseUrl,

      levelList: [
        {
          label: '省,市（州）, 县（市、区）',
          val: '省,市（州）, 县（市、区）'
        },
        {
          label: '市（州）, 县（市、区）',
          val: '市（州）, 县（市、区）'
        },
        {
          label: '县（市、区）',
          val: '县（市、区）'
        }
      ],
      processList: [
        {
          id: '',
          item: '',
          name: '',
          order: ''
        }
      ],
      styleList: [
        {
          label: '行政许可',
          val: '行政许可'
        }
      ],
      needList: [
        {
          label: '是',
          val: 'Y'
        },
        {
          label: '否',
          val: 'N'
        }
      ],

      addFormVisible: false,
      formLabelWidth: '120px',
      form: {
        item: '',
        name: '',
        process: '',
        need: '',
        require: '',
        permit: ''
      },
      fixFormVisible: false,
      form1: {
        item: '',
        name: '',
        process: '',
        need: '',
        require: '',
        permit: '',
        id: ''
      },

      selectIds: []
    }
  },
  watch: {
  },
  created: function () {
    this.getThemeFn()
  },
  methods: {
    changeThemeFn () {
      for (let i = 0; i < this.themeList.length; i++) {
        if (this.themeList[i].val === this.selectThemeId) {
          this.selectThemeName = this.themeList[i].label
          this.getMatterFn(this.selectThemeName)
          break
        }
      }
    },
    changeMatterFn () {
      for (let i = 0; i < this.matterList.length; i++) {
        if (this.matterList[i].val === this.selectMatterId) {
          this.selectMatterName = this.matterList[i].label
          this.getElementFn(this.selectMatterName)
          this.getItemStepsFn(this.selectMatterName)
          break
        }
      }
    },
    addBtnFn () {
      if (this.selectMatterName) {
        this.addFormVisible = true
        this.form.item = this.selectMatterName
        this.form.process = this.processList[0].id
        this.form.need = this.needList[0].val
      } else {
        this.$message({
          type: 'warning',
          message: '未选择主题!'
        })
      }
    },
    addSureFn () {
      let temp = {
        step_id: this.form.process,
        name: this.form.name.replace(/(^\s*)|(\s*$)/g, ''),
        requ: this.form.require,
        need: this.form.need,
        permit: this.form.permit,
        user_id: sessionStorage.user_id
      }
      console.log(temp)
      if (temp.name !== '' && temp.require !== '' && temp.permit !== '') {
        this.addFn(temp)
      } else {
        this.$message({
          type: 'warning',
          message: '请补全信息!'
        })
      }
    },
    handleEdit (index, row) {
      console.log(index, row)
      this.fixFormVisible = true
      this.form1.item = this.selectMatterName
      this.form1.name = row.name
      this.form1.need = row.need
      this.form1.require = row.require
      this.form1.permit = row.permit
      this.form1.id = row.id
      for (let i = 0; i < this.processList.length; i++) {
        if (row.step === this.processList[i].name) {
          this.form1.process = this.processList[i].id
          console.log(this.form1.process)
          console.log(this.processList[i].id)

          break
        } else {
          this.form1.process = this.processList[0].id
        }
      }
    },
    fixSureFn () {
      let temp = {
        id: this.form1.id,
        step_id: this.form1.process,
        name: this.form1.name.replace(/(^\s*)|(\s*$)/g, ''),
        requ: this.form1.require,
        need: this.form1.need,
        permit: this.form1.permit,
        user_id: sessionStorage.user_id
      }
      console.log(temp)
      if (temp.name !== '' && temp.require !== '' && temp.permit !== '') {
        this.fixFn(temp)
      } else {
        this.$message({
          type: 'warning',
          message: '请补全信息!'
        })
      }
    },
    clearBtnFn () {
      if (this.selectIds.length) {
        this.$confirm('确定删除所选要素', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let params = {
            id_list: this.selectIds,
            user_id: sessionStorage.user_id
          }
          this.deleteSingleFn(params)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      } else {
        this.$message({
          type: 'warning',
          message: '未选择要素!'
        })
      }
    },
    handleDelete (index, row) {
      console.log(row)
      this.$confirm('确定删除要素：' + row.name, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let params = {
          id_list: [row.id],
          user_id: sessionStorage.user_id
        }
        this.deleteSingleFn(params)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    async getThemeFn () {
      try {
        let params = {}
        let res = await this.$api.unsplash.getSubjects(params)
        console.log(res)
        this.themeList = []
        if (res.errno === '0') {
          for (let i = 0; i < res.data.subjects.length; i++) {
            this.themeList.push({
              label: res.data.subjects[i].name,
              val: res.data.subjects[i].id
            })
          }
          this.selectThemeId = this.themeList[0].val
          this.selectThemeName = this.themeList[0].label
          this.getMatterFn(this.selectThemeName)
        } else {
          this.$alert('服务器错误', '警告', {
            confirmButtonText: '确定',
            callback: action => {
            }
          })
        }
      } catch (e) {
        console.log(e)
      }
    },
    async getMatterFn (theme) {
      try {
        let params = {
          subject: theme
        }
        let res = await this.$api.unsplash.getMatters(params)
        console.log(res)
        if (res.errno === '0') {
          this.matterList = []
          for (let i = 0; i < res.data.items.length; i++) {
            this.matterList.push({
              label: res.data.items[i].name,
              val: res.data.items[i].id
            })
          }
          this.selectMatterId = this.matterList[0].val
          this.selectMatterName = this.matterList[0].label
          console.log(this.selectMatterName)
          this.getItemStepsFn(this.selectMatterName)
          this.getElementFn(this.selectMatterName)
        } else {
          this.$alert('服务器错误', '警告', {
            confirmButtonText: '确定',
            callback: action => {
            }
          })
        }
      } catch (e) {
        console.log(e)
      }
    },
    async getElementFn (matter) {
      try {
        let params = {
          item: matter
        }
        let res = await this.$api.unsplash.postElements(params)
        console.log(res)
        if (res.errno === '0') {
          this.tableData = res.data
          this.tableData = this.addSequence(this.tableData)
        } else {
          this.$alert('服务器错误', '警告', {
            confirmButtonText: '确定',
            callback: action => {
            }
          })
        }
      } catch (e) {
        console.log(e)
      }
    },
    async getItemStepsFn (matter) {
      try {
        let params = {
          item: matter
        }
        let res = await this.$api.unsplash.getItemSteps(params)
        if (res.errno === '0') {
          this.processList = res.data
        } else {
          this.$alert('服务器错误', '警告', {
            confirmButtonText: '确定',
            callback: action => {
            }
          })
        }
      } catch (e) {
        console.log(e)
      }
    },
    async addFn (params) {
      try {
        let res = await this.$api.unsplash.postAddElement(params)
        if (res.errno === '0') {
          this.addFormVisible = false
          this.$message({
            type: 'success',
            message: '增加要素成功'
          })
          this.getElementFn(this.selectMatterName)
        } else {
          this.$message({
            type: 'warning',
            message: '增加要素失败'
          })
        }
      } catch (e) {
        console.log(e)
      }
    },
    async fixFn (params) {
      try {
        let res = await this.$api.unsplash.postfixElement(params)
        console.log(res)
        if (res.errno === '0') {
          this.fixFormVisible = false
          this.$message({
            type: 'success',
            message: '修改要素成功'
          })
          this.getElementFn(this.selectMatterName)
        } else {
          this.$message({
            type: 'warning',
            message: '修改要素失败'
          })
        }
      } catch (e) {
        console.log(e)
      }
    },
    async deleteSingleFn (params) {
      try {
        let res = await this.$api.unsplash.postClearElement(params)
        console.log(res)
        if (res.errno === '0') {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.selectIds = []
          this.getElementFn(this.selectMatterName)
        } else {
          this.myAlert('服务器错误', '警告', this)
        }
      } catch (e) {
        console.log(e)
      }
    },
    handleSelectionChange (selection, row) { // 单击
      console.log(selection)
      this.selectIds = []
      for (let i = 0; i < selection.length; i++) {
        this.selectIds.push(selection[i].id)
      }
    },
    selectAllFn (selection) { // 全选
      console.log(selection)
      this.selectIds = []
      for (let i = 0; i < selection.length; i++) {
        this.selectIds.push(selection[i].id)
      }
    },
    headerCellStyle ({ row, column, rowIndex, columnIndex }) {
      if (rowIndex > -1) {
        return {
          'text-align': 'center'
        }
      }
    },
    cellStyle ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex >= 0) {
        return {
          'text-align': 'center'
        }
      }
    },
    myAlert (txt, title, self) {
      self.$alert(txt, title, {
        confirmButtonText: '确定',
        callback: action => {
        }
      })
    },
    addSequence (temp) {
      for (let i = 0; i < temp.length; i++) {
        temp[i].index = i + 1
      }
      return temp
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .functionalArea{
    height: 80px;
  }
  .headerCellStyle{
    text-align: center;
  }

</style>



